import { Directive, ElementRef , Input   } from '@angular/core';
import { CdsInternalControlLabel } from '@cds/core/forms';

@Directive({ selector: 'cds-internal-control-label' })
export class CdsInternalControlLabelDirective {
  protected element: CdsInternalControlLabel;

  get disabled() {
    return this.element.disabled;
  }
  @Input() set disabled(value: boolean | '') {
    this.element.disabled = value === '' ? true : value;
  };
  get action() {
    return this.element.action;
  }
  @Input() set action(value) {
    this.element.action = value;
  };


  constructor(elementRef: ElementRef) {
    this.element = elementRef.nativeElement;
  }
}
